@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
*{margin:0;padding:0;font-family:'Montserrat', sans-serif;box-sizing:border-box}

#fp-nav ul li a span{background:white !important}.home{color:white;position:relative}.home #home__menu{position:absolute;z-index:10000;width:100vw;display:flex;flex-direction:row;justify-content:flex-end;align-items:center;padding:20px}.home #home__menu a{margin:0 10px}

.land{color:white;position:relative}.land__intro{display:flex;justify-content:center;align-items:center;flex-direction:column}.land__intro--big{font-size:2rem;margin:10px 0}@-webkit-keyframes scroll-ani{0%{opacity:1;top:29%}15%{opacity:1;top:50%}50%{opacity:0;top:50%}100%{opacity:0;top:29%}}@keyframes scroll-ani{0%{opacity:1;top:29%}15%{opacity:1;top:50%}50%{opacity:0;top:50%}100%{opacity:0;top:29%}}.land .mouse-scroll{display:inline-block;line-height:18px;font-size:13px;font-weight:normal;color:#7f8c8d;color:#fff;font-family:'proxima-nova', 'Helvetica Neue', Helvetica, Arial, sans-serif;letter-spacing:2px;margin-top:10%;text-decoration:none;overflow:hidden;position:absolute;left:50%;transform:translateX(-50%);bottom:20px}.land .mouse-scroll .mouse{cursor:pointer;position:relative;display:block;width:46px;height:73px;margin:0 auto 20px;box-sizing:border-box;border:3px solid #fff;border-radius:23px}.land .mouse-scroll .mouse .mouse-movement{position:absolute;display:block;top:29%;left:50%;width:8px;height:8px;margin:-4px 0 0 -4px;background:#fff;border-radius:50%;-webkit-animation:scroll-ani 4s linear infinite;animation:scroll-ani 4s linear infinite}.land .mouse-scroll .mouse-message{float:left;margin:0 0 0 7px;padding:0;-webkit-animation:scroll-ani 4s linear infinite;animation:scroll-ani 4s linear infinite}

.contact{text-align:center}.contact p{margin:10px 50px}.contact__intro{font-size:0.8rem;font-style:italic}

.fp-slidesNav ul{text-align:center}.fp-slidesNav ul li a span{background:white}

.sort-anime{text-align:center}

.project-card{text-align:center;background-color:white;color:black;width:calc(100vw - 110px);display:inline-block;padding:20px 50px;border-radius:10px;text-decoration:none;transition:all 0.5s;max-width:1200px}.project-card:hover{transform:scale(1.02)}.project-card__image{width:100%;margin:20px 0;border-radius:10px;border:2px solid black}

.dom-code-snippets{text-align:center;background-color:#ffbe0b}

